import React, { Component } from 'react';
import PitchGraph from './components/PitchGraph/PitchGraph';
import Header from './components/Header/Header';
import MoraCount from './components/MoraCount/MoraCount';
import PitchType from './components/PitchType/PitchType';

type AccentType = 'Atamadaka' | 'Heiban' | 'Nakadaka' | 'Odaka';
interface State {
	currentMode: AccentType;
	currentMora: number;
	currentDrop: number;
}
class App extends Component<{}, State> {
	constructor(props: any) {
		super(props);
		this.state = {
			currentMode: 'Atamadaka',
			currentMora: 1,
			currentDrop: 0,
		};

		this.modeHandler = this.modeHandler.bind(this);
		this.moraHandler = this.moraHandler.bind(this);
	}

	modeHandler(mode: AccentType) {
		if (mode === 'Nakadaka' && this.state.currentMora < 3) {
			this.setState({ currentMora: 3 });
		} else if (mode === 'Odaka' && this.state.currentMora < 2) {
			this.setState({ currentMora: 2 });
		}
		this.setState({ currentMode: mode });
	}

	moraHandler(mora: number) {
		this.setState({ currentMora: mora });
	}

	render() {
		return (
			<div>
				<Header mode={this.state.currentMode} />
				<PitchGraph
					mode={this.state.currentMode}
					mora={this.state.currentMora}
				/>
				<PitchType modeHandler={this.modeHandler} />
				<MoraCount
					currentMora={this.state.currentMora}
					mode={this.state.currentMode}
					moraHandler={this.moraHandler}
				/>
			</div>
		);
	}
}

export default App;
