import React from 'react';
import Mora from './Mora/Mora';
import Particle from './Particle/Particle';
import classes from './PitchGraph.module.css';

import pitchMode from '../../data/pitchModeColor';
import accentExampleList from '../../data/accentExampleList';
import audioList from '../../data/audioList';

function PitchGraph(props: { mode: string; mora: number }) {
	// Choose the correct example from the list
	const chosenExample = accentExampleList.find(
		(example) =>
			example.accentType === props.mode && example.moraCount === props.mora
	);

	return (
		<div className={classes.PitchGraph}>
			<div className={classes.Word}>
				<p>{chosenExample?.word}</p>
			</div>

			<div className={classes.MoraCollection}>
				{chosenExample?.accents.map((mora, index) => (
					<Mora
						key={`${mora}${index}`}
						type={mora.accent}
						content={mora.mora}
					/>
				))}

				{chosenExample?.particle && (
					<Particle
						type={chosenExample?.particle.accent}
						content={chosenExample?.particle.mora}
					/>
				)}
			</div>

			<div
				className={classes.HighPitch}
				style={{ background: pitchMode[props.mode].secondary }}
			>
				<p>High Pitch</p>
			</div>

			<div
				className={classes.LowPitch}
				style={{ background: pitchMode[props.mode].tertiary }}
			>
				<p>Low Pitch</p>
			</div>

			<button
				className={classes.PlayButton}
				onClick={() => audioList[`${props.mode}${props.mora}`].play()}
				style={{ background: pitchMode[props.mode].primary }}
			>
				Play Audio
			</button>
		</div>
	);
}

export default PitchGraph;
