import React from 'react';
import classes from './MoraCount.module.css';

function MoraCount(props: {
	currentMora: number;
	mode: string;
	moraHandler: (mora: number) => void;
}) {
	// Start, Nakadaka: 3, Odaka: 2
	let start: number;

	if (props.mode === 'Nakadaka') {
		start = 3;
	} else if (props.mode === 'Odaka') {
		start = 2;
	} else {
		start = 1;
	}

	let ButtonList = [];
	// Display for Disabled Button
	for (let j = 1; j < start; j++) {
		ButtonList.push(<button disabled>{j} Mora</button>);
	}

	// Display for Available Button
	for (let i = start; i <= 6; i++) {
		ButtonList.push(
			i === props.currentMora ? (
				// Active Button
				<button
					style={{ background: 'gray' }}
					onClick={() => props.moraHandler(i)}
				>
					{i} Mora
				</button>
			) : (
				// Normal Button
				<button onClick={() => props.moraHandler(i)}>{i} Mora</button>
			)
		);
	}

	return <div className={classes.MoraCount}>{ButtonList}</div>;
}

export default MoraCount;
