import React from 'react';
import classes from './PitchType.module.css';
import pitchMode from '../../data/pitchModeColor';

type AccentType = 'Atamadaka' | 'Heiban' | 'Nakadaka' | 'Odaka';

function PitchType(props: { modeHandler: (mode: AccentType) => void }) {
	return (
		<div className={classes.PitchType}>
			<button
				onClick={() => props.modeHandler('Atamadaka')}
				style={{ background: pitchMode.Atamadaka.tertiary }}
			>
				Atamadaka
			</button>
			<button
				onClick={() => props.modeHandler('Heiban')}
				style={{ background: pitchMode.Heiban.tertiary }}
			>
				Heiban
			</button>
			<button
				onClick={() => props.modeHandler('Nakadaka')}
				style={{ background: pitchMode.Nakadaka.tertiary }}
			>
				Nakadaka
			</button>
			<button
				onClick={() => props.modeHandler('Odaka')}
				style={{ background: pitchMode.Odaka.tertiary }}
			>
				Odaka
			</button>
		</div>
	);
}

export default PitchType;
