import React from 'react';
import classes from './Mora.module.css';

function Mora(props: {
	type: string | undefined;
	content: string | undefined;
}) {
	let moraTypeClasses = props.type === 'High' ? classes.High : classes.Low;
	return (
		<div className={`${classes.Mora} ${moraTypeClasses}`}>{props.content}</div>
	);
}

export default Mora;
